fieldset {
	border: none;
}
table {
	border-collapse: collapse;
}
input[type='checkbox'],
input[type='radio'] {
	margin-right: 5px;
	margin-left: 15px;
	&:first-child {
		margin-left: 0;
	}
}
// textarea,
// input {
// 	border-radius: 0;
// 	-webkit-border-radius: 0;
// 	-moz-border-radius: 0;
// }
select::-ms-expand {
	display: none;
}

input[type='submit'],
input[type='reset'],
input[type='text'],
input[type='password'],
input[type='email'],
textarea,
button,
select {
	border: none;
	outline: none;
	appearance: none;
	-moz-appearance: none;
	-webkit-appearance: none;
	border-radius: 0;
	-webkit-border-radius: 0;
	-moz-border-radius: 0;
}
